"use client"

import { useState } from "react"
import {
  Bot,
  Send,
  Sparkles,
  Eye,
  EyeOff,
  Paperclip,
  Mic,
  ChevronDown,
  Plus,
  FolderOpen,
  MessageCircle,
  Check,
  Settings,
  ChevronLeft,
} from "lucide-react"
import { cn } from "@/lib/utils"

const agents = [
  { id: 1, name: "BKLite ITSM 助手", version: "v1.2.4", active: true },
  { id: 2, name: "运维专家", version: "v1.0", active: true },
  { id: 3, name: "知识库助手", version: "v2.1", active: false },
]

export function ChatArea() {
  const [selectedAgent, setSelectedAgent] = useState(agents[0])
  const [agentDropdownOpen, setAgentDropdownOpen] = useState(false)
  const [message, setMessage] = useState("")
  const [showReasoning, setShowReasoning] = useState(false)
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false)

  return (
    <div className="flex-1 ml-[200px] h-screen flex">
      {/* Left Panel - Agent Selector & History */}
      <div className={cn(
        "h-full bg-white border-r border-gray-100 flex flex-col transition-all duration-300",
        sidebarCollapsed ? "w-0 overflow-hidden" : "w-[280px]"
      )}>
        {/* Agent Selector */}
        <div className="p-4 border-b border-gray-100">
          <div className="relative">
            <button
              type="button"
              onClick={() => setAgentDropdownOpen(!agentDropdownOpen)}
              className="w-full flex items-center gap-3 px-3 py-2.5 rounded-xl bg-gradient-to-r from-indigo-500 to-violet-500 text-white hover:shadow-lg hover:shadow-indigo-500/20 transition-all duration-200"
            >
              <div className="w-9 h-9 rounded-lg bg-white/20 flex items-center justify-center">
                <Bot className="w-5 h-5 text-white" />
              </div>
              <div className="flex-1 text-left">
                <p className="text-sm font-medium truncate">{selectedAgent.name}</p>
                <p className="text-xs text-white/70">{selectedAgent.version}</p>
              </div>
              <ChevronDown className={cn(
                "w-4 h-4 text-white/70 transition-transform",
                agentDropdownOpen && "rotate-180"
              )} />
            </button>

            {/* Agent Dropdown */}
            {agentDropdownOpen && (
              <div className="absolute top-full left-0 right-0 mt-2 bg-white rounded-xl shadow-xl border border-gray-100 z-50 overflow-hidden animate-in fade-in slide-in-from-top-2">
                <div className="p-2">
                  {agents.map((agent) => (
                    <button
                      key={agent.id}
                      type="button"
                      onClick={() => {
                        setSelectedAgent(agent)
                        setAgentDropdownOpen(false)
                      }}
                      className={cn(
                        "w-full flex items-center gap-3 px-3 py-2.5 rounded-lg transition-colors",
                        selectedAgent.id === agent.id
                          ? "bg-indigo-50 text-primary"
                          : "hover:bg-gray-50 text-foreground"
                      )}
                    >
                      <div className={cn(
                        "w-8 h-8 rounded-lg flex items-center justify-center",
                        selectedAgent.id === agent.id
                          ? "bg-gradient-to-br from-primary to-violet-500"
                          : "bg-gray-100"
                      )}>
                        <Bot className={cn("w-4 h-4", selectedAgent.id === agent.id ? "text-white" : "text-gray-500")} />
                      </div>
                      <div className="flex-1 text-left">
                        <p className="text-sm font-medium truncate">{agent.name}</p>
                        <p className="text-xs text-muted-foreground">{agent.version}</p>
                      </div>
                      {selectedAgent.id === agent.id && (
                        <Check className="w-4 h-4 text-primary" />
                      )}
                    </button>
                  ))}
                </div>
                <div className="p-2 border-t border-gray-100">
                  <button
                    type="button"
                    className="w-full flex items-center justify-center gap-2 px-3 py-2 rounded-lg text-sm font-medium text-primary hover:bg-indigo-50 transition-colors"
                  >
                    <Settings className="w-4 h-4" />
                    管理智能体
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* New Chat Button */}
        <div className="p-4">
          <button
            type="button"
            className="w-full flex items-center justify-center gap-2 px-4 py-3 rounded-xl bg-indigo-600 text-white text-sm font-medium hover:bg-indigo-700 shadow-lg shadow-indigo-500/20 transition-all"
          >
            <Plus className="w-5 h-5" />
            开启新对话
          </button>
        </div>

        {/* History Section */}
        <div className="flex-1 overflow-y-auto">
          <div className="px-4 py-2">
            <p className="text-xs font-medium text-muted-foreground">历史对话</p>
          </div>
          
          {/* Empty State */}
          <div className="flex flex-col items-center justify-center py-16 px-6 text-center">
            <div className="w-14 h-14 rounded-2xl bg-gray-50 flex items-center justify-center mb-4">
              <FolderOpen className="w-7 h-7 text-gray-300" />
            </div>
            <p className="text-sm text-muted-foreground">No data</p>
          </div>
        </div>
      </div>

      {/* Collapse Toggle Button */}
      <button
        type="button"
        onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
        className="absolute left-[480px] top-1/2 -translate-y-1/2 w-6 h-12 bg-white border border-gray-200 rounded-r-lg flex items-center justify-center text-gray-400 hover:text-gray-600 hover:bg-gray-50 transition-all z-10"
        style={{ left: sidebarCollapsed ? '200px' : '480px' }}
      >
        <ChevronLeft className={cn(
          "w-4 h-4 transition-transform",
          sidebarCollapsed && "rotate-180"
        )} />
      </button>

      {/* Main Chat Area */}
      <div className="flex-1 h-screen flex flex-col bg-white">
        {/* Header */}
        <header className="h-14 px-6 flex items-center justify-between border-b border-gray-100 bg-white">
          <h1 className="text-base font-medium text-foreground">对话</h1>
          <button
            type="button"
            onClick={() => setShowReasoning(!showReasoning)}
            className="flex items-center gap-2 px-3 py-1.5 rounded-lg text-sm text-muted-foreground hover:text-foreground hover:bg-gray-50 transition-all"
          >
            显示推理追踪
            {showReasoning ? (
              <Eye className="w-4 h-4" />
            ) : (
              <EyeOff className="w-4 h-4" />
            )}
          </button>
        </header>

        {/* Main Chat Area with Grid Pattern */}
        <div className="flex-1 grid-pattern overflow-y-auto">
          <div className="h-full flex flex-col items-center justify-center px-6 py-12">
            {/* Empty State */}
            <div className="flex flex-col items-center">
              <div className="w-20 h-20 rounded-full bg-gray-50 border border-gray-100 flex items-center justify-center mb-6">
                <MessageCircle className="w-10 h-10 text-gray-300" />
              </div>
              <p className="text-base text-muted-foreground">初始化中...</p>
            </div>
          </div>
        </div>

        {/* Chat Input */}
        <div className="p-6 bg-white border-t border-gray-100">
          <div className="max-w-3xl mx-auto">
            <div className="flex items-center gap-3 p-2 bg-gray-50 rounded-2xl border border-gray-200">
              <button
                type="button"
                className="p-2 rounded-lg text-gray-400 hover:text-gray-600 hover:bg-gray-100 transition-colors"
              >
                <Paperclip className="w-5 h-5" />
              </button>
              <input
                type="text"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                placeholder="输入消息..."
                className="flex-1 px-2 py-2 text-sm bg-transparent border-0 outline-none placeholder:text-muted-foreground"
              />
              <button
                type="button"
                className="p-2 rounded-lg text-gray-400 hover:text-gray-600 hover:bg-gray-100 transition-colors"
              >
                <Mic className="w-5 h-5" />
              </button>
              <button
                type="button"
                disabled={!message.trim()}
                className={cn(
                  "flex items-center justify-center w-10 h-10 rounded-xl transition-all duration-200",
                  message.trim()
                    ? "bg-gradient-to-r from-primary to-violet-500 text-white shadow-lg shadow-indigo-500/25 hover:shadow-xl"
                    : "bg-indigo-600 text-white"
                )}
              >
                <Send className="w-5 h-5" />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
